h1 {
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul {
  margin: 0;
}

.header {
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(330.24deg, #0000 31.06%, #000 108.93%), url("hero_background.90b14beb.png");
  background-position: center;
  background-repeat: no-repeat, no-repeat;
  background-size: cover, cover;
}

.hero-section {
  max-width: 1751px;
  margin: 0 auto;
  padding: 64px 84px 0;
}

.hero-section__header {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.hero-section__navigation ul {
  display: flex;
}

.hero-section__navigation ul li {
  margin-right: 50px;
  list-style-type: none;
}

.hero-section__navigation ul li a {
  color: #fff;
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  text-decoration: none;
}

.hero-section__search {
  align-items: center;
  display: flex;
}

.hero-section__search > img {
  width: 20px;
  height: 20px;
  cursor: pointer;
  margin-right: 14px;
}

.hero-section__search input {
  width: 60px;
  cursor: pointer;
  background-color: #0000;
  border: #0000;
  transition: width .5s;
}

.hero-section__search input::placeholder {
  color: #fff;
  font-size: 17px;
  font-weight: 400;
  line-height: 23px;
}

.hero-section__search input:focus {
  width: 150px;
  caret-color: #fff;
  border: 1px solid #fff;
  outline: #fff;
  transition: width .5s;
}

.hero-section__burger {
  height: 23px;
  width: 23px;
  cursor: pointer;
  z-index: -1;
  display: none;
  position: relative;
}

.hero-section__burger .burger-line {
  width: 100%;
  height: 3px;
  background-color: #fff;
  transition: all .3s linear;
  position: absolute;
  left: 0;
}

.hero-section__burger .burger-line-first {
  top: 0;
}

.hero-section__burger .burger-line-second, .hero-section__burger .burger-line-third {
  top: 50%;
  transform: translateY(-50%);
}

.hero-section__burger .burger-line-fourth {
  bottom: 0;
}

.hero-section__burger-active .burger-line-first, .hero-section__burger-active .burger-line-fourth {
  opacity: 0;
}

.hero-section__burger-active .burger-line-second {
  width: 25px;
  transform: translateY(-50%)rotate(45deg);
}

.hero-section__burger-active .burger-line-third {
  width: 25px;
  transform: translateY(-50%)rotate(-45deg);
}

.hero-section__navigation-mobile {
  width: 100%;
  height: 100vh;
  color: #ccc;
  z-index: -1;
  -webkit-backdrop-filter: blur(8px);
  backdrop-filter: blur(8px);
  background: #fff3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 10px;
  transition: left 1s cubic-bezier(.68, -.55, .27, 1.55), z-index 1s cubic-bezier(.68, -.55, .27, 1.55);
  display: flex;
  position: fixed;
  top: 0;
  left: -1000px;
}

.hero-section__navigation-mobile-active {
  z-index: 1;
  left: 0;
}

.hero-section__navigation-mobile > ul {
  list-style: none;
}

.hero-section__navigation-mobile > ul li {
  text-align: center;
  border-bottom: 3px solid #0000;
  margin-bottom: 50px;
  transition: all .5s;
}

.hero-section__navigation-mobile > ul li:hover {
  border-bottom: 3px solid #ffc178;
}

.hero-section__navigation-mobile > ul a {
  color: #fff;
  text-transform: capitalize;
  cursor: pointer;
  font-size: 96px;
  font-weight: 700;
  line-height: 100px;
  text-decoration: none;
}

.hero-section__search-mobile {
  width: 100%;
  justify-content: center;
  margin-top: 150px;
  margin-bottom: 60px;
  display: flex;
}

.hero-section__search-mobile > input {
  width: 80%;
  height: 110px;
  border: 1px solid #fff;
  font-size: 86px;
  font-weight: 700;
  line-height: 86px;
}

.hero-section__search-mobile > input::placeholder {
  text-align: center;
  font-size: 86px;
  font-weight: 700;
  line-height: 86px;
}

.hero-section__main {
  margin-top: 250px;
  display: flex;
}

.hero-section__main-title {
  flex: 3;
}

.hero-section__main-line {
  width: 72px;
  height: 2px;
  background-color: #ffc178;
  margin-right: 32px;
}

.hero-section__title-heading {
  text-transform: capitalize;
  color: #fff;
  width: 100%;
  font-size: 96px;
  line-height: 100px;
}

.hero-section__title-heading div {
  margin: 0 auto;
}

.hero-section__title-heading div:first-child {
  font-weight: 700;
}

.hero-section__title-wrapper {
  align-items: center;
  display: flex;
}

.hero-section__title-wrapper p {
  color: #ffc178;
  letter-spacing: 6px;
  text-transform: uppercase;
  font-size: 18px;
  font-weight: 700;
  line-height: 25px;
}

.hero-section__social-media {
  flex-direction: column;
  flex: 1;
  align-items: flex-start;
  margin-top: 72px;
  display: flex;
}

.hero-section__social-media > div {
  color: #fff;
  margin-bottom: 47px;
  margin-left: -30px;
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  transform: rotate(.25turn);
}

.hero-section__social-media img {
  margin-bottom: 26px;
}

.hero-section__scroolDown {
  color: #fff;
  justify-content: center;
  font-size: 18px;
  font-weight: 600;
  line-height: 25px;
  display: flex;
}

.hero-section__scroolDown > p {
  margin-right: 9px;
}

@media screen and (max-width: 1500px) {
  .hero-section__title-heading {
    font-size: 80px;
  }
}

@media screen and (max-width: 1240px) {
  .hero-section__title-heading {
