@media screen and (max-width: 800px) {
  .hero-section__navigation, .hero-section__search {
    display: none;
  }

  .hero-section__burger {
    z-index: 2;
    display: block;
  }

  .hero-section__search-mobile input, .hero-section__search-mobile::placeholder, .hero-section__navigation-mobile ul a {
    font-size: 5em;
  }
